body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#watermark {
  position: fixed;
  color: rgb(69, 69, 69);
  opacity: 0.3;
  bottom: 10px;
  right: 15px;
  text-align: right;
}

#wmText {
  font-size: 12px;
  opacity: 0.7;
}
#wmName {
  font-size: 25px;
}
#wmLink {
  position: relative;
  font-size: 10px;
  opacity: 0.7;
}
#wmLink a {
  text-decoration: none;
  color: rgb(69, 69, 69);
}

nav {
  /* background-color: green; */
  position: absolute;
  color: black;
  width: 100vw;
  height: var(--nav-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px;
}

#navbarLogo img {
  /* height: 80%; */
  height: 10vh;
  margin-top: 10%; /* 10% of height */
  margin-left: 10%; /* 10% of height */
  width: 100%; /* 100% of height */
  border-radius: 50%;
}

#navbarLinks {
  position: inherit;
  right: 20px;
}
#navbarLinks span {
  margin: 20px;
  color: black;

  text-decoration: none;
}

