:root {
  /* --primary-color: #04619f; */
  /* --secondary-color: #000000; */
  --primary-color: skyblue;
  --secondary-color: #ffffff;
  --font-color: white;
  --background: linear-gradient(
    45deg,
    var(--primary-color),
    var(--secondary-color)
  );

  --nav-height: 12vh;
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    /* background: black; */
  }
  to {
    opacity: 1;
    /* background: linear-gradient(45deg, #000, #555); */
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    /* background: black; */
  }
  to {
    opacity: 1;
    /* background: linear-gradient(45deg, #000, #555); */
  }
}
@-webkit-keyframes wait {
  from {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}
@keyframes wait {
  from {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}

.background {
  background: linear-gradient(
    45deg,
    skyblue,
    #ffffff);
  background: var(--background);
  position: fixed;
  width: 100%;
  min-height: 100vh;
  -webkit-animation: fadeIn 6s;
          animation: fadeIn 6s;
  z-index: -1;
}

.hide {
  display: none;
}

.contentx {
  position: absolute;
  top: 12vh;
  top: var(--nav-height);
  color: white;
  width: 100%;
  text-align: center;
  /* background-color: blueviolet; */
}

/* EQP LIST! */

.device {
  /* background: blue; */
  display: flex;
  justify-content: left;
  margin: 35px;
}

.deviceName {
  font-weight: bold;
  width: 90%;
  margin: 10px 20px;
  font-size: 35px;
  height: 100%;

  align-self: center;
  color: #5e5e5e;
}

.deviceDescription {
  /* background: red; */
  text-align: justify;
  display: block;
  color: black;
  margin: 20px;
}

.deviceImage {
  height: 25vh;
  border-radius: 10px;
}

.content {
  color: white;
  top: 0px;
  height: 100vh;
  width: 100%;
  margin: auto;
  text-align: center;
}

.landingPage {
  position: relative;
  top: 30vh;
}

.contentTitle {
  -webkit-animation: wait 1.5s, fadeIn 1.5s;
          animation: wait 1.5s, fadeIn 1.5s;
  -webkit-animation-delay: 0s, 1.5s;
          animation-delay: 0s, 1.5s;
  font-family: Brush Script MT;
  font-size: 200px;
  /* font-weight: bolder; */
  margin-bottom: 20px;
  color: lightgray;
  /* opacity: 0.7; */
}

.contentBody {
  -webkit-animation: wait 3s, fadeIn 8s;
          animation: wait 3s, fadeIn 8s;
  -webkit-animation-delay: 0s, 3s;
          animation-delay: 0s, 3s;
  color: darkgray;
  /* top: 20vh; */
}

.aboutTitle {
  /* background: green; */
  /* margin-top: 60px; */

  -webkit-animation: wait 1.5s, fadeIn 1.5s;

          animation: wait 1.5s, fadeIn 1.5s;
  -webkit-animation-delay: 0s, 1.5s;
          animation-delay: 0s, 1.5s;
  font-family: Marker Felt;
  font-size: 50px;
  font-weight: bolder;
  margin-bottom: 0px;
  /* margin-top: 0; */
  position: relative;
  /* margin-top: 20px; */
}

.aboutBody {
  position: relative;
  width: 100%;
  -webkit-animation: wait 3s, fadeIn 4s;
          animation: wait 3s, fadeIn 4s;
  -webkit-animation-delay: 0s, 3s;
          animation-delay: 0s, 3s;
  color: darkgray;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
}
.aboutFooterBody {
  position: relative;
  width: 100%;
  -webkit-animation: wait 4s, fadeIn 4s;
          animation: wait 4s, fadeIn 4s;
  -webkit-animation-delay: 0s, 4s;
          animation-delay: 0s, 4s;
  color: darkgray;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.aboutFooterTitle {
  -webkit-animation: wait 4s, fadeIn 1.5s;
          animation: wait 4s, fadeIn 1.5s;
  -webkit-animation-delay: 0s, 4s;
          animation-delay: 0s, 4s;
  font-family: Marker Felt;
  font-size: 30px;
  font-weight: bolder;
  margin-bottom: 0px;
  margin-top: 0;
  position: relative;
}

